.breadcrumb {
  --v-breadcrumb-gap: 0.6667rem;
  display: flex;
  gap: var(--v-breadcrumb-gap);
  color: var(--v-secondary);
  font-size: 0.7333rem;
}
.breadcrumb__item {
  display: flex;
  align-items: center;
  gap: var(--v-breadcrumb-gap);
  padding: 0;
}
.breadcrumb__icon {
  color: var(--v-yellow);
}
.breadcrumb__link {
  color: var(--v-secondary);
}
.breadcrumb__link {
  color: var(--v-white);
}